<template>
<div class="user-login-wrap">
    <div class="Box">
         <!--登录成功-->
        <div class="LRSBox" v-if="isSuccessLoginPage">
            <div class="title"><span>用户信息</span></div>
            <p><span class="colHeader">{{userInfo.username}}！</span></p>
            <p v-if="userInfo.username=='waltz'">
                <span class="colManager">您好，管理员！</span>
                <router-link to="/textP">进入管理</router-link>
            </p>
            <p v-else><span class="colHeader">您好，欢迎来到我的博客！</span></p>
            <p><span class="colHeader" @click="loginOut">退出</span></p>
        </div>

        <!--登录-->
        <div class="LRSBox" v-if="isShowLoginPage">
            <div class="title"><span>登录</span></div>
            <div class="inputBox">
                <span class="colHeader">用户名：</span>
                <input type="text" v-model="username"/>
            </div>
            <div class="inputBox">
                <span class="colHeader">密码：</span>
                <input type="password" v-model="password"/>
            </div>
            <div class="inputBox">
                <span class="colHeader" @click="toLogin">登录</span>
                <a @click="toRegisterPage">立即注册</a>
                <p class="warning textCenter"></p>
            </div>
        </div>

        <!--注册-->
        <div class="LRSBox" v-if="isShowRegisterPage">
            <div class="title"><span>注册</span></div>
            <div class="inputBox">
                <span class="colHeader">用户名：</span><input type="text" v-model="username"/>
            </div>
            <div class="inputBox">
                <span class="colHeader">密码：</span><input type="password" v-model="password"/>
            </div>
            <div class="inputBox">
                <span class="colHeader">密码：</span><input type="password" v-model="repassword"/>
            </div>
            <div class="inputBox">
                <span class="colHeader"  @click="toRegister">注册</span>
                <a @click="toLoginPage">立即登录</a>
                <p class="warning textCenter"></p>
            </div>
        </div>
    </div>
</div>
</template>
 
<script>
import carousel from '../components/carousel'
import axios from 'axios'
export default{
    props: [''],
    components: {
        carousel,
    },
    
    data() {
        return {
            username: '', 
            password: '',
            repassword: '',
            userInfo: '',
            isShowRegisterPage: false, //是否显示注册页面
            isShowLoginPage: true, //是否显示登陆页面
            isSuccessLoginPage: false, //是否显示成功登陆页面
        }
    },
    
    mounted(){
        // this.initData();
    },
    
    methods: {
        /**
         * 去注册页面
         */
        toRegisterPage() {
            this.isShowLoginPage = false;
            this.isShowRegisterPage = true;
        },

        /**
         * 去登陆页面
         */
        toLoginPage() {
            this.isShowLoginPage = true;
            this.isShowRegisterPage = false;
        },

        /**
         * 登陆
         */
        toLogin() {
            debugger
            let url = tripDoc.api + tripDoc.activeAPI.login_post;
            axios
            .post(url, {
                username: this.username,
                password: this.password
            })
            .then( (res) => {
                debugger
                if(res.data.code == 0) {
                    this.message = res.data.message;
                    this.userInfo = res.data.userInfo;
                    if(this.message == '登录成功') {
                        this.isSuccessLoginPage = true;
                        this.isShowLoginPage = false;
                    }
                    this.$set(res.data, 'userInfo', res.data.userInfo)
                }
            })
            .catch( (err) => {
                console.log(err)
            })
        },

        /**
         * 注册新用户
         */
        toRegister() {
            let url = tripDoc.api + tripDoc.activeAPI.register_post;
            axios
            .post(url, {
                username: this.username,
                password: this.password,
                repassword: this.repassword
            })
            .then( (res) => {
                debugger
                if(res.data.code == 0) {
                    if(this.message == '注册成功') {
                        this.isShowRegisterPage = false;
                        this.isShowLoginPage = true;
                    }
                    this.userInfo = res.data.userInfo;
                    this.$set(res.data, 'userInfo', res.data.userInfo)
                }
            })
            .catch( (err) => {
                console.log(err)
            })
        },

        /**
         * 退出登陆
         */
        loginOut() {
            // 清理缓存
            let url = tripDoc.api + tripDoc.activeAPI.loginOut_get;
            axios
            .get(url, {})
            .then( (res) => {
                debugger
                if(!res.code){
                    window.location.reload();
                }
            })
            .catch( (err) => {
                console.log(err)
            })
        },

        /**
         * 初始化数据
         */
        initData() {
            this.$http.get('/api/').then((response) => {
                console.log(response)
            })
        }
    },
    
    }
</script>
 
<style lang='less' scoped>
    .Box{
        width: 8rem;
        height: 8rem;
        background: #f5f5f5;
        margin: .625rem auto;
        padding-top: 0.2rem;
        .LRSBox{
            width: 7rem;
            height: 4rem;
            border: solid .0625rem #ccc;
            margin: 1rem auto;
            .title{
                height: 1rem;
                line-height: 1rem;
            }
            .inputBox{
                height: 0.6rem;
                line-height: 0.6rem;
                .colHeader{
                    width: 1.2rem;
                    display: inline-block;
                    text-align: right;
                }
            }
        }
    }
</style>



